.Wrapper{
  display: grid;
  grid-template-columns: 0.9fr;
  grid-template-rows: 0.15fr 0.15fr;
  height: 100%;

}
.But-contain {
  margin: 10%;
  background: linear-gradient(90deg, #FFBF85 0%, #FF7981 100%);
  border-radius: 15px;
  align: center;
}
.But {
  font-family: Tahoma;
  font-size: 20px;
  line-height: 24px;

  color: #FFF;
}
